import React from 'react';
import styled from '@emotion/styled';

import { colors } from '../../styles/colors';
import {css} from "@emotion/react";

interface CallToActionState {
  isOpen: boolean;
}

export class CalltoactionModal extends React.Component<any, CallToActionState> {
  state = { isOpen: false };

  componentWillUnmount() {
    this.unCallToActionEsc();
  }

  escFunction = (event: KeyboardEvent) => {
    if (event.key === 'Escape') {
      this.close();
    }
  };

  CallToActionEsc() {
    document.addEventListener('keydown', this.escFunction, false);
  }

  unCallToActionEsc() {
    document.removeEventListener('keydown', this.escFunction, false);
  }

  open = () => {
    this.setState({ isOpen: true });
    this.CallToActionEsc();
  };

  close = () => {
    this.setState({ isOpen: false });
    this.unCallToActionEsc();
  };

  render() {
    return (
      <CallToActionOverlay open={this.state.isOpen}>
        <CallToActionOverlayClose onClick={this.close} />
        <CallToActionOverlayContent>

          <h3 css={CallToActionTitle}>Есть вопросы о гидродинамической очистке?</h3>
          <p>Позвоните нам:</p>
          <button
              className="button_primary"
              type="button"
              onClick={(e) => {
                e.preventDefault();
                window.open('tel:+375336540704', '_blank');
              }}
          >МТС: +375 33 6540704
          </button>
          <button
              className="button_secondary"
              type="button"
              onClick={(e) => {
                e.preventDefault();
                window.open('tel:+375299699499', '_blank');
              }}
          >А1: +375 29 96 99 499
          </button>

          <p>Или напишите по электронной почте: <a href="mailto:info@cgs.by">info@cgs.by</a></p>


        </CallToActionOverlayContent>
      </CallToActionOverlay>
    );
  }
}

